.solar_systm {
    position: relative;
    height: 100vh;
    width: 100vw;
    transform-style: preserve-3d;
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

* {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.solar {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: black;
}

.menu {
    position: absolute;
    transform-origin: center center;
    border-radius: 50%;
}

.preview {
    width: 40px;
    height: 40px;
    background-color: yellow;
    border-radius: 50%;
    position: absolute;
}

.planet_description {
    position: absolute;
    text-align: center;
    color: white;
}

.planet9, .planet8, .planet7, .planet6, .planet5, .planet4, .planet3, .planet2, .planet1 {
    appearance: none;
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.pluto {
    animation: rotatePluto 160s linear infinite;
}

@keyframes rotatePluto {
    from {
        transform: rotateZ(0deg) translateX(200px);
    }
    to {
        transform: rotateZ(360deg) translateX(200px);
    }
}

