.solar_systm {
    position: relative;
    height: 100vh;
    width: 100vw;
    transform-style: preserve-3d;
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

* {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.solar {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: black;
}

.menu {
    position: absolute;
    transform-origin: center center;
    border-radius: 50%;
}

.preview {
    width: 40px;
    height: 40px;
    background-color: yellow;
    border-radius: 50%;
    position: absolute;
}

.planet_description {
    position: absolute;
    text-align: center;
    color: white;
}

/* 행성 위치 조정 */
.pluto {
    top: 10%;
    left: 50%;
    animation: rotatePluto 160s linear infinite;
}

.neptune {
    top: 20%;
    left: 50%;
    animation: rotateNeptune 140s linear infinite;
}

.uranus {
    top: 30%;
    left: 50%;
    animation: rotateUranus 120s linear infinite;
}

.saturn {
    top: 40%;
    left: 50%;
    animation: rotateSaturn 100s linear infinite;
}

.jupiter {
    top: 50%;
    left: 50%;
    animation: rotateJupiter 80s linear infinite;
}

.mars {
    top: 60%;
    left: 50%;
    animation: rotateMars 60s linear infinite;
}

.earth {
    top: 70%;
    left: 50%;
    animation: rotateEarth 40s linear infinite;
}

.venus {
    top: 80%;
    left: 50%;
    animation: rotateVenus 30s linear infinite;
}

.mercury {
    top: 90%;
    left: 50%;
    animation: rotateMercury 20s linear infinite;
}

/* 행성 회전 애니메이션 */
@keyframes rotatePluto {
    from {
        transform: rotateZ(0deg) translateX(200px);
    }
    to {
        transform: rotateZ(360deg) translateX(200px);
    }
}

@keyframes rotateNeptune {
    from {
        transform: rotateZ(0deg) translateX(180px);
    }
    to {
        transform: rotateZ(360deg) translateX(180px);
    }
}

@keyframes rotateUranus {
    from {
        transform: rotateZ(0deg) translateX(160px);
    }
    to {
        transform: rotateZ(360deg) translateX(160px);
    }
}

@keyframes rotateSaturn {
    from {
        transform: rotateZ(0deg) translateX(140px);
    }
    to {
        transform: rotateZ(360deg) translateX(140px);
    }
}

@keyframes rotateJupiter {
    from {
        transform: rotateZ(0deg) translateX(120px);
    }
    to {
        transform: rotateZ(360deg) translateX(120px);
    }
}

@keyframes rotateMars {
    from {
        transform: rotateZ(0deg) translateX(100px);
    }
    to {
        transform: rotateZ(360deg) translateX(100px);
    }
}

@keyframes rotateEarth {
    from {
        transform: rotateZ(0deg) translateX(80px);
    }
    to {
        transform: rotateZ(360deg) translateX(80px);
    }
}

@keyframes rotateVenus {
    from {
        transform: rotateZ(0deg) translateX(60px);
    }
    to {
        transform: rotateZ(360deg) translateX(60px);
    }
}

@keyframes rotateMercury {
    from {
        transform: rotateZ(0deg) translateX(40px);
    }
    to {
        transform: rotateZ(360deg) translateX(40px);
    }
}